import Vue from 'vue'
import App from './App'
import router from './router'
// store
import store from './store/app'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'

Vue.use(MintUI)

Vue.config.productionTip = false

// // rem 
// import RemConfig from 'libs/rem_config'

// 
import urls from 'libs/urls.js'
Vue.prototype.$urls = urls // URLS  this.$urls

// ajax
import axios from 'libs/http.js'
Vue.prototype.$http = axios // URLS  this.$https

// Set default values using the global configuration.
if (process.env.NODE_ENV === 'development') {
    require('libs/common/mocktest') // mockjs
} else {
    // 
}

new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>'
})


