<template>
  <div class="date">
    <datetimePicker :closeOnClickModal="false" v-model="value" @confirm="handleConfirm" @cancel="checkinCancel" cancelText="" confirmText="" ref="datePicker" type="date" :visibleItemCount="3" :isNeedDay='isNeedDay' :endDate='endDate' year-format="{value} " month-format="{value} " date-format="{value} ">
    </datetimePicker>
  </div>
</template>
<script>
import datetimePicker from "../common/datetimePicker";
export default {
  components: {
    datetimePicker
  },
  props: {
    //  model
    showModel: {
      type: Boolean,
      default: false
    },
    //  model //false  1 
    isNeedDay: {
      type: Boolean,
      default: true
    },
    // 
    dateValue: {
      type: [Date],
      default: new Date()
    }
  },
  data() {
    return {
      value: this.dateValue,
      // startDate: new Date(), //  
      endDate: new Date() //
    };
  },
  watch: {
    showModel(value) {
      if (value) {
        this.openPicker();
      } else {
        this.checkinCancel();
      }
    }
  },
  methods: {
    checkinCancel() {
      // console.log('cancel')
      this.$refs.datePicker.close();
      this.$emit("hide", 2);
    },
    handleConfirm(date) {
      // console.log("date:", date);
      this.$refs.datePicker.close();
      this.submit(date)
    },
    // 
    submit(date) {
      this.$emit("ok", date);
    },
    openPicker() {
      this.$refs.datePicker.open();
    },
  },
  created() {},
  mounted() {}
};

</script>
<style lang="less" >
.date {
  .mint-popup {
    z-index: 99999999!important;
    border-radius: 0;
  }
  .mint-datetime-cancel {
    color: #e2e2e2;
  }
}

</style>



