<template>
  <div class="box">
    <div class="box-title">
      <img src="../../../assets/app/title.png" alt="">
      <font></font>
    </div>
    <div class="box-tabs">
      <mt-swipe :auto="0">
        <mt-swipe-item>991</mt-swipe-item>
        <mt-swipe-item>992</mt-swipe-item>
        <mt-swipe-item>99</mt-swipe-item>
      </mt-swipe>
      <img class="right" src="../../../assets/app/right.png" alt="">
    </div>
    <div id="myBusinessChart" class="businessLine"></div>
    <div class="box-data">
      <div class="box-data-item ">
        <img class="item-img" src="../../../assets/app/yesday.png" alt="">
        <div class="box-datas">
          <div class="box-data-title"></div>
          <div class="box-data-data">6666</div>
        </div>
      </div>
      <div class="box-data-item">
        <img class="item-img" src="../../../assets/app/today.png" alt="">
        <div class="box-datas">
          <div class="box-data-title"></div>
          <div class="box-data-data">6666</div>
        </div>
      </div>
      <div class="box-data-item">
        <img class="item-img" src="../../../assets/app/precent.png" alt="">
        <div class="box-datas">
          <div class="box-data-title"><img src="../../../assets/app/blueUp.png" alt=""> </div>
          <div class="box-data-data">6666</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Swipe, SwipeItem } from 'mint-ui';
// 
import echarts from "echarts/lib/echarts";
// 
import "echarts/lib/chart/line";
// legendtitle
import "echarts/lib/component/tooltip";
// import "echarts/lib/component/title";
import "echarts/lib/component/legend";

export default {
  props: ['list'],
  data () {
    return {}
  },
  props: ['xData', 'yDataSame', 'yDataRing'],
  watch: {
    xData () {
      this.drawLine(this.xData, this.yDataSame, this.yDataRing)
    }
  },
  mounted () {
    this.drawLine(this.xData, this.yDataSame, this.yDataRing)
  },
  methods: {
    drawLine (xData, yDataSame, yDataRing) {
      // domecharts
      // const myBusinessChart = this.$echarts.init(document.getElementById('myBusinessChart'))
      const myBusinessChart = echarts.init(document.getElementById('myBusinessChart'))
      const option = {
        //  
        tooltip: {
          // //  
          trigger: 'axis',
          // trigger: 'item',
          backgroundColor: 'rgba(255,255,255,1)', //rgba  
          color: '#43484D',
          borderWidth: 1,
          borderRadius: 5,
          borderColor: '#eee',
          shadowColor: 'rgba(0, 0, 0, 0.5)',
          shadowBlur: 10,
          shadowOffsetY: 20,
          padding: 0,
          textStyle: {
            color: '#43484D',
          },
          position ([x, y]) { //
            // console.log('x', x)
            // console.log('y', y)
            return [x - 45, y - 45]
          },
          axisPointer: {
            type: 'line',
            lineStyle: {
              color: '#9dcae9',
              width: 1,
              type: 'dashed'
            },
            crossStyle: {
              color: '#9dcae9',
              width: 1,
              type: 'dashed'
            },
            shadowStyle: {
              color: 'rgba(150,150,150,0.3)',
              width: 1,
              type: 'default'
            }
          },
          formatter (data) {
            // console.log('data[0].dataIndex', data[0].dataIndex)
            const index = data[0].dataIndex
            let domData = `<div class='business-tips'><div class='business-tips-top'> ${xData[index]} </div> <br/> <div class='business-tips-content'> &nbsp;&nbsp;&nbsp;: <font class='color-yellow'>${yDataRing[index]} </font> <br/>  :  <font class='color-green'> ${yDataSame[index]} </font></div></div>`

            return domData //
          },
          // extraCssText:'background:rgba(0, 0, 0, 0.5);'  
        },
        color: ["#86D8DD", "#0384DF"], //
        // legend: { //
        //   x: '59%',
        //   y: '5%',
        //   data: ["", ""]
        // },
        calculable: true,
        xAxis: [{
          type: 'category',
          boundaryGap: true, //xAxisboundaryGapfalsetrue
          axisLabel: {
            interval: 0, // 
            color: "#86939E" //
          },
          axisTick: { //
            show: true, //
            alignWithLabel: true, // boundaryGap  true 
            interval: 'auto', // 0  1 2
            inside: true, //
            // length:5,                //
            // lineStyle:mylineStyle
          },
          axisLine: {
            lineStyle: {
              color: '#e2e2e2',
              width: 1, //  
            }
          },
          data: xData
        }],
        yAxis: [

          {
            show: false,
            type: 'value',
            splitLine: { // 
              show: false
            },
            axisLabel: {
              color: "#86939E" //
            },
            axisTick: { //
              show: false, //
              alignWithLabel: true, // boundaryGap  true 
              interval: 'auto', // 0  1 2
              inside: true, //
              // length:5,                //
              // lineStyle:''
            },
            axisLine: {
              lineStyle: {
                color: '#e2e2e2',
                width: 1, //  
              }
            },
          }
        ],
        series: [{
          name: '',
          type: 'line',
          // stack: '',
          //smooth : false,
          //symbol: null,         // 
          symbolSize: 5, // 
          //symbolRotate : null,  // 
          showAllSymbol: false, // 
          itemStyle: {
            normal: {
              // color: ,
              label: {
                show: false
                // position: 'top''right'
                //           'inside'|'left'|'right'|'top'|'bottom'
                // textStyle: null      // TEXTSTYLE
              },
              lineStyle: {
                width: 2,
                type: 'solid',
                shadowColor: 'rgba(0,0,0,0)', //
                shadowBlur: 5,
                shadowOffsetX: 3,
                shadowOffsetY: 3
              }
            },
            emphasis: {
              // color: ,
              label: {
                show: false
                // position: 'top''right'
                //           'inside'|'left'|'right'|'top'|'bottom'
                // textStyle: null      // TEXTSTYLE
              }
            }
          },
          data: yDataRing
        },
        {
          name: '',
          type: 'line',
          // stack: '',
          //smooth : false,
          //symbol: null,         // 
          symbolSize: 5, // 
          //symbolRotate : null,  // 
          showAllSymbol: false, // 
          itemStyle: {
            normal: {
              // color: ,
              label: {
                show: false
                // position: 'top''right'
                //           'inside'|'left'|'right'|'top'|'bottom'
                // textStyle: null      // TEXTSTYLE
              },
              lineStyle: {
                width: 2,
                type: 'solid',
                shadowColor: 'rgba(0,0,0,0)', //
                shadowBlur: 5,
                shadowOffsetX: 3,
                shadowOffsetY: 3
              }
            },
            emphasis: {
              // color: ,
              label: {
                show: false
                // position: 'top''right'
                //           'inside'|'left'|'right'|'top'|'bottom'
                // textStyle: null      // TEXTSTYLE
              }
            }
          },
          data: yDataSame
        }
        ]
      };

      // 
      myBusinessChart.setOption(option);
    }
  }
}

</script>
<style lang="less">
@font-color1: #43484d; //
@font-color2: #86939e; //
@font-color3: #0384df; //
@font-color4: #fff; //
.box-title {
  color: @font-color1;
  height: 0.84rem;
  line-height: 0.84rem;
  text-align: center;
  font-size: 0.31rem;
  font-family: "PingFang-SC-Heavy";
  border-bottom: 0.02rem solid #eee;
  img {
    position: relative;
    width: 0.33rem;
    top: 0.05rem;
    margin-right: 0.1rem;
  }
}

.box-tabs {
  position: relative;
  height: 0.8rem;
  .mint-swipe {
    width: 1.5rem;
    overflow: visible;
  }
  .mint-swipe-indicators {
    position: absolute;
    bottom: -0.4rem;
    left: 0.25rem;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  .mint-swipe-items-wrap > div {
    top: 0.28rem;
    left: 0.23rem;
    font-size: 0.28rem;
  }
  .mint-swipe-indicator.is-active {
    background: @font-color3;
    opacity: 1;
  }
  .right {
    height: 0.31rem;
    width: 0.15rem;
    position: absolute;
    right: 0.35rem;
    top: 0.25rem;
  }
}

.box-data {
  height: 1.5rem;
  padding: 0.2rem 0.22rem;
  .box-data-item {
    float: left;
    position: relative;
    height: 1.36rem;
    width: 1.24rem;
    border: 0.02rem solid #eee;
    padding-left: 1rem;
    -webkit-box-shadow: 0 0 0.1rem #eee;
    box-shadow: 0 0 0.1rem #eee;
    margin-right: 0.1rem;
    .item-img {
      position: absolute;
      left: 0.32rem;
      top: 0.56rem;
      width: 0.28rem;
    }
    .box-datas {
      width: 1.3rem;
    }
    .box-data-title {
      margin-top: 0.27rem;
      font-size: 0.22rem;
      color: @font-color2;
      img {
        width: 0.15rem;
        height: 0.26rem;
      }
    }
    .box-data-data {
      margin-top: 0.1rem;
      font-size: 0.42rem;
      color: @font-color3;
    }
  }
  .box-data-item:last-child {
    margin-right: 0.02rem;
  }
}

.businessLine {
  height: 3.5rem; // width: 7rem;
  width: 100%; // margin-left: 0.25rem;
}

.business-tips {
  width: 1.7rem;
  padding: 0.07rem 0.2rem;
  box-shadow: 0.1rem 0.1rem 0.1rem #ccc;
}

.business-tips-content {
  font-family: "PingFang-SC-Medium";
  color: #43484d;
  font-size: 0.25rem;
  margin-top: -0.15rem;
}

.business-tips-top {
  color: #43484d;
  border-bottom: 0.02rem solid #eaeaea;
  text-align: center;
}

.color-yellow {
  margin-left: 0.19rem; // color: #FC9D2E;
}

.color-green {
  margin-left: 0.19rem; // color: #269513;
}

.font-style {
}
</style>



