<template>
  <div id="echarts">
    <div id="myChart" class="line"></div>
  </div>
</template>
<script>
// 
import { addWaterMarker } from "libs/common/common";
// 
import echarts from "echarts/lib/echarts";
// 
import "echarts/lib/chart/line";
// legendtitle
import "echarts/lib/component/tooltip";
// import "echarts/lib/component/title";
import "echarts/lib/component/legend";

export default {
  props: ['list'],
  data () {
    return {}
  },
  props: ['xData', 'yDataSame', 'yDataRing', 'thanTitle', 'thanTitle2'], //xDatax yDataSame yDataRing
  watch: {
    xData () {
      this.drawLine(this.xData, this.yDataSame, this.yDataRing)
    }
  },
  mounted () {
    this.drawLine(this.xData, this.yDataSame, this.yDataRing)
    // let dom = document.getElementById('echarts')
    // addWaterMarker(dom,'4G')
  },
  methods: {
    drawLine (xData, yDataRing, yDataSame, ) {
      let self = this
      let showLastMonth = true,
        showLastYear = true //  ''  

      // domecharts
      const myChart = echarts.init(document.getElementById('myChart'))
      const option = {
        //  
        tooltip: {
          // //  
          trigger: 'axis',
          // trigger: 'item',
          backgroundColor: 'rgba(255,255,255,1)', //rgba  
          color: '#43484D',
          borderWidth: 1,
          borderRadius: 5,
          borderColor: '#eee',
          shadowColor: 'rgba(0, 0, 0, 0.5)',
          shadowBlur: 10,
          shadowOffsetY: 20,
          padding: 0,
          textStyle: {
            color: '#43484D',
          },
          position ([x, y]) { //
            // console.log('x', x)
            // console.log('y', y)
            return [x - 55, y - 45]
          },
          axisPointer: {
            type: 'line',
            lineStyle: {
              color: '#9dcae9',
              width: 1,
              type: 'dashed'
            },
            crossStyle: {
              color: '#9dcae9',
              width: 1,
              type: 'dashed'
            },
            shadowStyle: {
              color: 'rgba(150,150,150,0.3)',
              width: 1,
              type: 'default'
            }
          },
          formatter (data) {
            const index = data[0].dataIndex
            let domData = ``
            let domSame = Number(yDataSame[index]) >= 0 ? "<font class='color-yellow'>" : "<font class='color-green'>"
            let domRing = Number(yDataRing[index]) >= 0 ? "<font class='color-yellow'>" : "<font class='color-green'>"
            if (showLastMonth && showLastYear) {
              domData = "<div class='tips'><div class='tips-top'> " + xData[index] + "</div> <br/> <div class='tips-content'>" + self.thanTitle + domRing + yDataRing[index] + "%</font> <br/>" + self.thanTitle2 + domSame + yDataSame[index] + "%</font></div></div>"

            } else if (showLastMonth) {
              domData = "<div class='tips'><div class='tips-top'> " + xData[index] + "</div> <br/> <div class='tips-content'>" + self.thanTitle + domRing + yDataRing[index] + "%</font> </div></div>"
            } else if (showLastYear) {
              domData = "<div class='tips'><div class='tips-top'> " + xData[index] + "</div> <br/> <div class='tips-content'>" + self.thanTitle2 + domSame + yDataSame[index] + "%</font></div></div>"
            }

            return domData //,
          },
          // extraCssText:'background:rgba(0, 0, 0, 0.5);'  
        },
        color: [], //
        legend: { //
          x: '55%',
          y: '5%',
          data: [] //['', ""]
        },
        calculable: true,
        xAxis: [{
          type: 'category',
          boundaryGap: true, //xAxisboundaryGapfalsetrue
          axisLabel: {
            interval: 0, // 
            color: "#86939E" //
          },
          axisTick: { //
            show: true, //
            alignWithLabel: true, // boundaryGap  true 
            interval: 'auto', // 0  1 2
            inside: true, //
            // length:5,                //
            // lineStyle:mylineStyle
          },
          axisLine: {
            lineStyle: {
              color: '#e2e2e2',
              width: 1, //  
            }
          },
          data: xData
        }],
        yAxis: [{
          type: 'value',
          splitLine: { // 
            show: false
          },
          axisLabel: {
            color: "#86939E" //
          },
          axisTick: { //
            show: true, //
            alignWithLabel: true, // boundaryGap  true 
            interval: 'auto', // 0  1 2
            inside: true, //
            // length:5,                //
            // lineStyle:mylineStyle
          },
          axisLine: {
            lineStyle: {
              color: '#e2e2e2',
              width: 1, //  
            }
          },
        }],
        series: [] //
      };

      let lastMonth = {
        name: this.thanTitle,
        type: 'line',
        // stack: '',
        //smooth : false,
        //symbol: null,         // 
        symbolSize: 5, // 
        //symbolRotate : null,  // 
        showAllSymbol: false, // 
        itemStyle: {
          normal: {
            // color: ,
            label: {
              show: false
              // position: 'top''right'
              //           'inside'|'left'|'right'|'top'|'bottom'
              // textStyle: null      // TEXTSTYLE
            },
            lineStyle: {
              width: 2,
              type: 'solid',
              shadowColor: 'rgba(0,0,0,0)', //
              shadowBlur: 5,
              shadowOffsetX: 3,
              shadowOffsetY: 3
            }
          },
          emphasis: {
            // color: ,
            label: {
              show: false
              // position: 'top''right'
              //           'inside'|'left'|'right'|'top'|'bottom'
              // textStyle: null      // TEXTSTYLE
            }
          }
        },
        data: yDataRing
      },
        lastYear = {
          name: this.thanTitle2,
          type: 'line',
          // stack: '',
          //smooth : false,
          //symbol: null,         // 
          symbolSize: 5, // 
          //symbolRotate : null,  // 
          showAllSymbol: false, // 
          itemStyle: {
            normal: {
              // color: ,
              label: {
                show: false
                // position: 'top''right'
                //           'inside'|'left'|'right'|'top'|'bottom'
                // textStyle: null      // TEXTSTYLE
              },
              lineStyle: {
                width: 2,
                type: 'solid',
                shadowColor: 'rgba(0,0,0,0)', //
                shadowBlur: 5,
                shadowOffsetX: 3,
                shadowOffsetY: 3
              }
            },
            emphasis: {
              // color: ,
              label: {
                show: false
                // position: 'top''right'
                //           'inside'|'left'|'right'|'top'|'bottom'
                // textStyle: null      // TEXTSTYLE
              }
            }
          },
          data: yDataSame
        }
      option.legend.data = []
      if (showLastMonth && showLastYear) { //'', "" 
        option.series = [lastMonth, lastYear]
        option.legend.data = [self.thanTitle, self.thanTitle2]
        option.color = ["#0384df", "#86d8dd"]

      }
      option.legend.data = [self.thanTitle, self.thanTitle2]
      // 
      myChart.setOption(option);
    }
  }
}

</script>
<style lang="less">
.line {
  height: 5.7rem;
  padding-left: 0.1rem;
  div:nth-child(2) {
    z-index: 999 !important;
  }
}

.tips {
  min-width: 1.7rem;
  max-width: 2rem;
  padding: 5px 10px;
  box-shadow: 6px 6px 5px #ccc;
}

.tips-content {
  font-family: 'PingFang-SC-Medium';
  color: #43484d;
  font-size: 0.25rem;
  margin-top: -15px;
}

.tips-top {
  color: #43484d;
  border-bottom: 1px solid #eaeaea;
  text-align: center;
}

.color-yellow {
  margin-left: 0.1rem;
  color: #fc9d2e;
}

.color-green {
  margin-left: 0.1rem;
  color: #269513;
}
</style>



