<template>
  <div class="pages">
    <mt-header title="">
    </mt-header>
    <Business :xData="xData" :yDataSame="yDataSame" :yDataRing="yDataRing"></Business>
    <div class="bg"></div>
    <KeyFigures></KeyFigures>
    <div class="bg-line"></div>
    <ReportForm></ReportForm>
    <div class="bg"></div>
    <MyApps></MyApps>
    <div class="bg"></div>
    <Information></Information>
  </div>
</template>
<script>
import { Progress } from 'mint-ui';
import { Toast } from 'mint-ui';
import { Indicator } from 'mint-ui';
import Business from 'com/app/index/business.vue';
import KeyFigures from 'com/app/index/keyFigures.vue';
import ReportForm from 'com/app/index/reportForm.vue';
import MyApps from 'com/app/index/myApps.vue';
import Information from 'com/app/index/information.vue';
export default {
  components: {
    Business,
    KeyFigures,
    MyApps,
    ReportForm,
    Information,

  },
  data() {
    return {
      isFirstEnter: false, // false
      headerData: {
        bestTitle: '',
        worstTitle: '',
        rankTitle: '',
        bestAreaName: '',
        worstAreaName: '',
        bestData: '6.666',
        worstData: '6.666',
        family: '666666666',
        lastMonth: '6.666',
        lastYear: '6.666',
      },
      initList: [],
      tabActiveType: 1,
      activeType: true,
      xData: [], //X
      yDataSame: [], //
      yDataRing: [], //
      queryParams: {
        "pageSize": 20,
        "pageNo": 1
      },
    }
  },
  methods: {
    getData2() {
      this.$http.post("/a/wechat/donate/findPageSlDonateRecord", {
        json: JSON.stringify(this.queryParams)
      }).then(res => {
        console.log('res:', res)
      }).catch(error => {
        console.log(error)
      })
    },
    changeLineChart(id) {
      console.log('cityId', id)
      this.activeType = false
      this.getData(id)
    },
    changeDateType(type) {
      this.tabActiveType = type
      if (type === 1) {
        this.headerData.rankTitle = ''
      } else {
        this.headerData.rankTitle = ''
      }
      this.getData(type)
    },
    changeType() {
      this.activeType = !this.activeType
    },
    getData(type) {
      Indicator.open();
      this.$http.get(this.$urls.getLineList, {
        type: type,
      }).then((res) => {
        console.log('res.data.data.list:', res.data.data.list)
        Indicator.close()
        this.initList = res.data.data.list;
        this.xData = []
        this.yDataSame = []
        this.yDataRing = []
        this.initList.forEach((i) => {
          this.xData.push(i.date)
          this.yDataSame.push(i.rate)
          this.yDataRing.push(i.ringRatio)
        })
      }).catch(error => {
        Indicator.close()
        console.log(error);
      });
    },
    goToRouter(id) {
      this.$router.push(`/addAreaDetails/${id}`)
    },

  },
  created() {
    // 
    // keep-alive2+
    this.isFirstEnter = true;
    this.getData()
  },
  activated() {
    if (!this.$route.meta.isBack || this.isFirstEnter) {
      // isBackfalse
      // isFirstEntertrue
      // this.initList = [] // 
      this.getData();
      console.log(('activated,add'))
    }
    // falseisBacktrue
    this.$route.meta.isBack = false
    // falseisBacktrue
    this.isFirstEnter = false;

  },
  beforeRouteEnter(to, from, next) {
    //metametaisBack
    if (from.name == 'addAreaDetails') {
      to.meta.isBack = true;
      //addAreaDetails
    }
    next();
  },

}

</script>
<style lang="less" scoped>
  .bg{
    width: 100%;
    height: 0.22rem;
    background-color:#F3F4F6;
  }
  .bg-line{
    width: 100%;
    height: 0.11rem;
    background-color:#F3F4F6;
  }
</style>
<style lang="less">
@main-color: #43484D;
@main-color-2: #86939E;
@main-color-green: #269513;
@main-color-yellow: #FC9D2E;
.font-family-fn(@family) {
  font-family: @family;
}

::-webkit-scrollbar {
  display: none; //
}

.pages {
  -webkit-overflow-scrolling: touch; // ios
  padding-bottom: 0.7rem;
  .mint-header {
    height: 0.8rem;
    background-color: #0384DF;
    z-index: 9;
    .mint-header-title {
      font-size: 0.36rem;
      font-weight: normal;
      .font-family-fn("PingFang-SC-Medium");
    }
  }
}



</style>



