// 
const iScollProbe = require('iscroll/build/iscroll-probe');
let scroller = null;
let Selector = "";
export function createIScroller(selector) {
  Selector = selector;
  scroller = new iScollProbe(Selector, {
    preventDefault: false, // 
    probeType: 3, // iscroll-probe.js  probeType  1  probeType  2  probeType  3   
    mouseWheel: true, //
    scrollX: true, // x
    scrollY: true, // y
    // momentum: false,
    lockDirection: false,
    snap: false, //Options.snap:true// 
    //snapSpeed: 400,
    scrollbars: false, //
    freeScroll: false, //
    deceleration: 0.0001, // 0.01,:0.0006
    disableMouse: true, //
    disablePointer: true, //
    disableTouch: false, //
    eventPassthrough: false, // IScroll 
    bounce: false //
  });
  scroller.on('scroll', updatePosition);
  scroller.on('scrollEnd', updatePosition);
  scroller.on('beforeScrollStart', function () {
    scroller.refresh();
  });

  function updatePosition() {
    let frozenCols = document.querySelectorAll(selector + ' table tr td.cols');
    let frozenRows = document.querySelectorAll(selector + ' table tr th.rows');
    let frozenCrosses = document.querySelectorAll(selector + ' table tr th.cross');
    for (let i = 0; i < frozenCols.length; i++) {
      frozenCols[i].style.transform = 'translate(' + -1 * this.x + 'px, 0px) translateZ(0px)';
    }
    for (let i = 0; i < frozenRows.length; i++) {
      frozenRows[i].style.transform = 'translate(0px, ' + -1 * this.y + 'px) translateZ(0px)';
    }
    for (let i = 0; i < frozenCrosses.length; i++) {
      frozenCrosses[i].style.transform = 'translate(' + -1 * this.x + 'px,' + -1 * this.y + 'px) translateZ(0px)';
    }
  }

  return scroller;
}

export function refreshScroller() {
  if (scroller === null) {
    console.error("scroller");
    return;
  }
  setTimeout(() => {
    scroller.refresh();
    scroller.scrollTo(0, 0);
    let frozenCols = document.querySelectorAll(Selector + ' table tr td.cols');
    let frozenRows = document.querySelectorAll(Selector + ' table tr th.rows');
    let frozenCrosses = document.querySelectorAll(Selector + ' table tr th.cross');
    for (let i = 0; i < frozenCols.length; i++) {
      frozenCols[i].style.transform = 'translate(0px, 0px) translateZ(0px)';
    }
    for (let i = 0; i < frozenRows.length; i++) {
      frozenRows[i].style.transform = 'translate(0px, 0px) translateZ(0px)';
    }
    for (let i = 0; i < frozenCrosses.length; i++) {
      frozenCrosses[i].style.transform = 'translate(0px, 0px) translateZ(0px)';
    }
  }, 0);
}

// 
let scrollerAuto = null;
let SelectorAuto = "";
export function createIScrollerAuto(
  selector,
  preventDefault = false,
  probeType = 3,
  mouseWheel = true,
  scrollX = true,
  scrollY = true,
  lockDirection = false,
  snap = false,
  scrollbars = false,
  freeScroll = false,
  deceleration = 0.0001,
  disableMouse = true,
  disablePointer = true,
  disableTouch = false,
  eventPassthrough = false,
  bounce = false
) {
  SelectorAuto = selector;
  scrollerAuto = new iScollProbe(SelectorAuto, {
    preventDefault: preventDefault, // 
    probeType: probeType, // iscroll-probe.js  probeType  1  probeType  2  probeType  3   
    mouseWheel: mouseWheel, //
    scrollX: scrollX, // x
    scrollY: scrollY, // y
    // momentum: true, ///
    lockDirection: lockDirection,
    snap: snap, //Options.snap:true// 
    //snapSpeed: 400,
    scrollbars: scrollbars, //
    freeScroll: freeScroll, //
    deceleration: deceleration, // 0.01,:0.0006
    disableMouse: disableMouse, //
    disablePointer: disablePointer, //
    disableTouch: disableTouch, //
    eventPassthrough: eventPassthrough, // IScroll 
    bounce: bounce, //
    // useTransform:false,
    // HWCompositing:false,
  });
  scrollerAuto.on('scroll', updatePositionAuto);
  scrollerAuto.on('scrollEnd', updatePositionAuto);
  scrollerAuto.on('beforeScrollStart', function () {
    scrollerAuto.refresh();
  });
  
  function updatePositionAuto() {
    let frozenCols = document.querySelectorAll(selector + ' table tr td.cols');
    let frozenRows = document.querySelectorAll(selector + ' table tr th.rows');
    let frozenCrosses = document.querySelectorAll(selector + ' table tr th.cross');

    for (let i = 0; i < frozenCols.length; i++) {
      frozenCols[i].style.transform = 'translate(' + -1 * this.x + 'px, 0px) translateZ(0px)';
      // frozenCols[i].style.top = -1 * this.x + 'px'
      // console.log('this.x:',this.x)
    }
    for (let i = 0; i < frozenRows.length; i++) {
      frozenRows[i].style.transform = 'translate(0px, ' + -1 * this.y + 'px) translateZ(0px)';
      // frozenRows[i].style.left = -1 * this.y + 'px';
      // console.log('this.y:',this.y)
    }
    for (let i = 0; i < frozenCrosses.length; i++) {
      frozenCrosses[i].style.transform = 'translate(' + -1 * this.x + 'px,' + -1 * this.y + 'px) translateZ(0px)';
      frozenCrosses[i].style.transform = 'translate(' + -1 * this.x + 'px,' + -1 * this.y + 'px) translateZ(0px)';
      // frozenRows[i].style.left = 0 + 'px';
      // frozenCols[i].style.top = 0 + 'px'
      // console.log('frozenCols[i].style.top:',this.x)
    }
  }

  return scrollerAuto;
}



