// 

//QueryString
export function getQueryString() {
  var result = location.search.match(new RegExp("[\?\&][^\?\&]+=[^\?\&]+", "g"));
  if (result == null) {
    return "";
  }
  for (var i = 0; i < result.length; i++) {
    result[i] = result[i].substring(1);
  }
  return result;
}

//QueryString
export function getQueryStringByName(name) {
  var result = location.search.match(new RegExp("[\?\&]" + name + "=([^\&]+)", "i"));
  if (result == null || result.length < 1) {
    return "";
  }
  return result[1];
}

//QueryString
export function getQueryStringByIndex(index) {
  if (index == null) {
    return "";
  }
  var queryStringList = getQueryString();
  if (index >= queryStringList.length) {
    return "";
  }
  var result = queryStringList[index];
  var startIndex = result.indexOf("=") + 1;
  result = result.substring(startIndex);
  return result;
}

export function setCookie(cName, value, expiredays) {
  if (expiredays > 0 && expiredays != "100") {
    var exdate = new Date()
    exdate.setDate(exdate.getDate() + expiredays)
    document.cookie = cName + '=' + escape(value) +
      ((expiredays == null) ? '' : ';expires=' + exdate.toGMTString())
  }
  if (expiredays === "100") {
    var exdate = new Date('2118-01-01 00:00:00')
    document.cookie = cName + '=' + escape(value) +
      ((expiredays == null) ? '' : ';expires=' + exdate.toGMTString())
  }

};
export function getCookie(cName) {
  if (document.cookie.length > 0) {
    var cStart = document.cookie.indexOf(cName + '=')
    if (cStart !== -1) {
      cStart = cStart + cName.length + 1
      var cEnd = document.cookie.indexOf(';', cStart)
      if (cEnd === -1) cEnd = document.cookie.length
      return unescape(document.cookie.substring(cStart, cEnd))
    }
  }
  return ''
};

export function delCookie(name) {
  var exp = new Date();
  exp.setTime(exp.getTime() - 1);
  var cval = getCookie(name);
  if (cval != null)
    document.cookie = name + "=" + cval + ";expires=" + exp.toGMTString();
}

//cookie
export function clearCookie(name) {
  setCookie(name, "", -1);
}
/**
 * 
 * @param  {[type]} originList  
 * @param  {[type]} newList     
 * @return {[type]}            [description]
 */
export function mergeList(originList, newList) {
  var resultList = []
  for (var i = 0; i < originList.length; i++) {
    resultList.push(originList[i])
  }
  for (var j = 0; j < newList.length; j++) {
    resultList.push(newList[j])
  }
  return resultList
};
